
header .cvbtn:hover{
  background-color:rgb(89 114 90) !important;
}
.title{
  font-family: "Cookie", serif;
  font-weight: 400;
  font-style: normal;
  font-size: 30px;
}
/*---------one by one type letter--------- */
.animation{
display: inline-block;
overflow: hidden;
white-space: nowrap;
animation: example 2s linear 0s infinite alternate;
}

@keyframes example{ 
from {
  width: 0;
}
to {
  width: 300px;
}
} 


/*---------section 3--------- */
.section-3 .cardhover{
  position: relative;
  border-radius: 50px 15px;
  cursor:default;
}

.section-3 .overplay{
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  background-color: rgb(163, 151, 151);
  opacity: 0;
  transition: .10s  ease-in-out;
  border-radius:45px 10px;
}

.cardhover:hover .overplay{
  opacity: 0.9;
}

.text {
  color: white;
  font-size: 30px;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50% );
  text-align: center;
}

/*---------section 5--------- */
.section-5 .circle{
  background-color: rgb(222, 225, 18);
  border:4px solid rgb(222,225,18);
  box-shadow: 0px 0px 20px white;
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

.section-5 .circle .line{
  background-color: #ee2c12;
  width: 5px;
  height: 85ch;
  border-radius:10px;
  position: relative;
  top: 25px;
  right: 3px;
}

.section-5 .mytext{
  text-align:justify;
}

/*---------footer--------- */
footer ul{
  list-style-type: none;
}

footer ul li a{
  text-decoration: none;
  color:black;
}

footer h5 a{
  text-decoration: none;
  color: black;
}


/*---------whastapp--------- */
.whatsapp{
  position: relative;
  bottom: 20px;
  right: 20px;
}

.whatsapp img{
  position:fixed !important;
  bottom:50px;
  right: 40px;
  animation:beat .4s infinite alternate;;
}
@keyframes beat{
	to { transform: scale(1.1); }
}

/*---------right to left page show--------- */
.sec1, .sec3, .sec5{
  animation: revealing1;
  animation-timeline: view(block);
  animation-range: cover 0% cover 50%;
  margin: 0px;
  overflow: hidden;
}

@keyframes revealing1 {
  from {
    color: gray;
    margin-left: 100vw;
    filter: blur(50px);
    opacity: 0.5;
    overflow: hidden;
  }
  to {
    filter: blur(0px);
    opacity: 1;
  }
}

/*---------left ot right page show--------- */
.sec2, .sec4{
  animation: revealing2;
  animation-timeline: view(block);
  animation-range: cover 0% cover 50%;
  /* margin: 0px; */
  overflow: hidden;
}

@keyframes revealing2 {
  from {
    color: gray;
    margin-left: -100rem;
    filter: blur(50px);
    opacity: 0.5;
    
  }
  to {
    margin: 0px;
    filter: blur(0px);
    opacity: 1;
  }
}
